import { Controller, Node, Selection, Commands, Point } from '@ali/4ever-cangjie';

export function selectNode(controller: Controller, node: Node | null) {
  if (!node) return controller;
  const anchor = Point.createFromParams(node.key, 'before');
  const focus = Point.createFromParams(node.key, 'after');
  const newSelection = Selection.create({
    anchor,
    focus,
  });
  controller.command(Commands.select, newSelection);
  return controller;
}
